'use client'

import { useEffect, useState } from 'react'
import { supabase } from '../lib/supabaseClient'
import { useRouter } from 'next/navigation'
import { Search, LogIn, Filter, Star, Heart, Truck } from 'lucide-react'

export default function Home() {
  const [settings, setSettings] = useState(null)
  const [products, setProducts] = useState([])
  const [filteredProducts, setFilteredProducts] = useState([])
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedCategory, setSelectedCategory] = useState('Todos')
  const [priceRange, setPriceRange] = useState([0, 10000])
  const [showFilters, setShowFilters] = useState(false)
  const router = useRouter()

  useEffect(() => {
    loadCatalogData()
  }, [])

  useEffect(() => {
    filterProducts()
  }, [products, searchTerm, selectedCategory, priceRange])

  const loadCatalogData = async () => {
    const { data: prods } = await supabase.from('products').select('*')
    setProducts(prods || [])

    const { data: sets } = await supabase.from('settings').select('*').limit(1)
    if (sets && sets.length > 0) {
      setSettings(sets[0])
    }
  }

  const filterProducts = () => {
    let filtered = products

    if (searchTerm) {
      filtered = filtered.filter(product =>
        product.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        product.reference_code?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        product.brand?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        product.description?.toLowerCase().includes(searchTerm.toLowerCase())
      )
    }

    if (selectedCategory !== 'Todos') {
      filtered = filtered.filter(product => product.brand === selectedCategory)
    }

    filtered = filtered.filter(product => product.price >= priceRange[0] && product.price <= priceRange[1])

    setFilteredProducts(filtered)
  }

  const categories = ['Todos', ...new Set(products.map(p => p.brand).filter(Boolean))]

  return (
    <div className="min-h-screen bg-gray-100">
      {/* Header */}
      <header className="bg-yellow-400 shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between py-3">
            <div className="flex items-center space-x-4">
              {settings?.logo_url && (
                <img src={settings.logo_url} alt="Logo" className="h-8 w-8" />
              )}
              <h1 className="text-xl font-bold text-gray-900">Rep-Vendas</h1>
            </div>

            <div className="flex-1 max-w-2xl mx-8">
              <div className="relative">
                <input
                  type="text"
                  placeholder="Buscar produtos..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="w-full pl-4 pr-10 py-2 border border-gray-300 rounded-sm focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                />
                <Search className="absolute right-3 top-2.5 h-5 w-5 text-gray-400" />
              </div>
            </div>

            <button
              onClick={() => router.push('/login')}
              className="text-sm text-gray-700 hover:text-gray-900"
            >
              <LogIn className="h-5 w-5 inline mr-1" />
              Entrar
            </button>
          </div>
        </div>
      </header>

      {/* Navigation */}
      <nav className="bg-white border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center space-x-6 py-3 overflow-x-auto">
            {categories.slice(0, 8).map((category) => (
              <button
                key={category}
                onClick={() => setSelectedCategory(category)}
                className={`text-sm whitespace-nowrap ${
                  selectedCategory === category
                    ? 'text-blue-600 font-medium border-b-2 border-blue-600'
                    : 'text-gray-700 hover:text-gray-900'
                }`}
              >
                {category}
              </button>
            ))}
          </div>
        </div>
      </nav>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        <div className="flex">
          {/* Sidebar Filters */}
          <aside className={`w-64 pr-6 ${showFilters ? 'block' : 'hidden'} md:block`}>
            <div className="bg-white p-4 rounded shadow-sm">
              <h3 className="font-medium text-gray-900 mb-4">Filtros</h3>

              <div className="mb-6">
                <label className="block text-sm font-medium text-gray-700 mb-2">Pre√ßo</label>
                <div className="space-y-2">
                  <input
                    type="range"
                    min="0"
                    max="10000"
                    value={priceRange[1]}
                    onChange={(e) => setPriceRange([priceRange[0], parseInt(e.target.value)])}
                    className="w-full"
                  />
                  <div className="flex justify-between text-sm text-gray-600">
                    <span>R$ {priceRange[0]}</span>
                    <span>R$ {priceRange[1]}</span>
                  </div>
                </div>
              </div>

              <div className="mb-6">
                <label className="block text-sm font-medium text-gray-700 mb-2">Categoria</label>
                <select
                  value={selectedCategory}
                  onChange={(e) => setSelectedCategory(e.target.value)}
                  className="w-full border border-gray-300 rounded px-3 py-2"
                >
                  {categories.map((category) => (
                    <option key={category} value={category}>{category}</option>
                  ))}
                </select>
              </div>

              <button
                onClick={() => {
                  setSearchTerm('')
                  setSelectedCategory('Todos')
                  setPriceRange([0, 10000])
                }}
                className="w-full bg-gray-100 text-gray-700 py-2 px-4 rounded hover:bg-gray-200"
              >
                Limpar Filtros
              </button>
            </div>
          </aside>

          {/* Products Grid */}
          <main className="flex-1">
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-2xl font-bold text-gray-900">
                {selectedCategory === 'Todos' ? 'Todos os Produtos' : selectedCategory}
              </h2>
              <button
                onClick={() => setShowFilters(!showFilters)}
                className="md:hidden flex items-center text-sm text-gray-600"
              >
                <Filter className="h-4 w-4 mr-1" />
                Filtros
              </button>
            </div>

            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
              {filteredProducts.map((product) => (
                <div key={product.id} className="bg-white border border-gray-200 rounded shadow-sm hover:shadow-md transition-shadow">
                  <div className="relative">
                    {product.image_url ? (
                      <img
                        src={product.image_url}
                        alt={product.name}
                        className="w-full h-48 object-cover rounded-t"
                      />
                    ) : (
                      <div className="w-full h-48 bg-gray-200 flex items-center justify-center rounded-t">
                        <span className="text-gray-500 text-sm">Sem imagem</span>
                      </div>
                    )}
                    <button className="absolute top-2 right-2 p-1 bg-white rounded-full shadow-sm hover:bg-gray-50">
                      <Heart className="h-4 w-4 text-gray-400" />
                    </button>
                  </div>

                  <div className="p-4">
                    <h3 className="text-sm font-medium text-gray-900 mb-1 line-clamp-2">
                      {product.name}
                    </h3>
                    <p className="text-xs text-gray-600 mb-2">{product.brand}</p>

                    <div className="flex items-center mb-2">
                      <div className="flex items-center">
                        <Star className="h-3 w-3 text-yellow-400 fill-current" />
                        <Star className="h-3 w-3 text-yellow-400 fill-current" />
                        <Star className="h-3 w-3 text-yellow-400 fill-current" />
                        <Star className="h-3 w-3 text-yellow-400 fill-current" />
                        <Star className="h-3 w-3 text-yellow-400 fill-current" />
                      </div>
                      <span className="text-xs text-gray-600 ml-1">(0)</span>
                    </div>

                    <div className="flex items-center text-xs text-green-600 mb-2">
                      <Truck className="h-3 w-3 mr-1" />
                      Frete gr√°tis
                    </div>

                    <div className="flex items-baseline space-x-2">
                      <span className="text-lg font-bold text-gray-900">
                        R$ {product.price?.toFixed(2)}
                      </span>
                      <span className="text-xs text-gray-500 line-through">
                        R$ {(product.price * 1.2)?.toFixed(2)}
                      </span>
                    </div>

                    <button className="w-full mt-3 bg-blue-600 text-white py-2 px-4 rounded text-sm font-medium hover:bg-blue-700 transition-colors">
                      Comprar
                    </button>
                  </div>
                </div>
              ))}
            </div>

            {filteredProducts.length === 0 && (
              <div className="text-center py-16">
                <div className="text-6xl mb-4">üîç</div>
                <h3 className="text-xl font-medium text-gray-900 mb-2">Nenhum produto encontrado</h3>
                <p className="text-gray-600">Tente ajustar sua busca ou filtros.</p>
              </div>
            )}
          </main>
        </div>
      </div>

      {/* Footer */}
      <footer className="bg-gray-900 text-white py-8 mt-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
            <div>
              <h4 className="font-bold mb-4">Sobre</h4>
              <ul className="space-y-2 text-sm text-gray-400">
                <li><a href="#" className="hover:text-white">Institucional</a></li>
                <li><a href="#" className="hover:text-white">Carreiras</a></li>
                <li><a href="#" className="hover:text-white">Imprensa</a></li>
              </ul>
            </div>
            <div>
              <h4 className="font-bold mb-4">Ajuda</h4>
              <ul className="space-y-2 text-sm text-gray-400">
                <li><a href="#" className="hover:text-white">Como comprar</a></li>
                <li><a href="#" className="hover:text-white">Pagamento</a></li>
                <li><a href="#" className="hover:text-white">Entrega</a></li>
              </ul>
            </div>
            <div>
              <h4 className="font-bold mb-4">Vendedor</h4>
              <ul className="space-y-2 text-sm text-gray-400">
                <li><a href="#" className="hover:text-white">Como vender</a></li>
                <li><a href="#" className="hover:text-white">Desenvolvimento</a></li>
              </ul>
            </div>
            <div>
              <h4 className="font-bold mb-4">Contato</h4>
              <p className="text-sm text-gray-400">
                {settings?.email || 'contato@empresa.com'}<br />
                {settings?.phone || 'Telefone'}
              </p>
            </div>
          </div>
          <div className="border-t border-gray-800 mt-8 pt-8 text-center text-sm text-gray-400">
            <p>&copy; 2025 Rep-Vendas. Todos os direitos reservados.</p>
          </div>
        </div>
      </footer>
    </div>
  )
}